















































.timeline {
  position: relative;
  padding: 20px 10%;
  &__node {
    display: flex;
    position: relative;
    box-sizing: border-box;
    padding: 20px;
    background-color: #fff;
    border-radius: 4px;
    text-align: left;
    & img {
      display: block;
      width: 200px;
      height: 200px;
      margin-right: 20px;
    }
    &__content {
      display: flex;
      flex-direction: column;
      &__desc {
        flex: 1;
      }
    }
    &__action {
      font-size: 12px;
      float: right;
      padding: 4px 10px;
      line-height: 1;
      cursor: pointer;
      &:hover {
        color: #3366ff;
      }
    }
    &__time {
      font-size: 12px;
      position: absolute;
      top: calc(50% - 1.5em);
      right: -6em;
      padding: 1em;
      // background: white;
      // border-radius: 0% 100% 100% 0%/ 100% 100% 100% 100%;
      z-index: 2;
      &::before {
        position: absolute;
        top: calc(50% - 1.5em);
        right: -6em;
        width: 1em;
        height: 1em;
        background: white;
      }
    }

    &::after {
      content: "";
      position: absolute;
      top: 0;
      right: -5%;
      width: 1px;
      height: 100%;
      background-color: rgb(93, 97, 108);
      // box-shadow: 40px 0 0 gray;
    }
    &::before {
      content: "";
      position: absolute;
      top: 50%;
      right: -10%;
      width: 10%;
      height: 1px;
      background-color: rgb(93, 97, 108);
    }
  }
}
